<template>
  <div :class="classe">
    <div class="form-group">
      <label :for="id_html" v-html="label" v-if="label"></label>
      <validation-provider
        :name="formated_label"
        :rules="rules"
        :id="id_html"
        v-slot="v"
        class="d-block"
        :mode="mode"
      >
        <input
          :id="id_html"
          class="form-control"
          type="number"
          :class="v.classes"
          v-model.number="input.value"
          :placeholder="placeholder"
          @keyup="event_input"
        />
      </validation-provider>
    </div>
  </div>
</template>
<script>
import { ValidationProvider } from "vee-validate";
/**
 * validations
 */
import "/siteweb/PluginsModules/stephane888/wbu-components/src/components/Fomrs/Elements/valids.js";

export default {
  name: "InputNumber",
  props: {
    label: {
      type: [String, Boolean],
      default: "input type text"
    },
    id_html: {
      type: String,
      default: "edit-inputtypetext"
    },
    classe: {
      type: String,
      default: ""
    },
    required: {
      type: Boolean,
      default: false
    },
    input: {
      type: Object,
      default: function() {
        return {
          value: ""
        };
      }
    },
    placeholder: {
      type: [String, Number],
      default: ""
    },
    rules: {
      type: [String],
      default: ""
    },
    mode: {
      type: String,
      default: "aggressive"
    }
  },
  methods: {
    event_input: function() {
      this.$emit("ev_input", this.input.value); // by default
    }
  },
  components: {
    "validation-provider": ValidationProvider
  },
  computed: {
    formated_label: {
      get() {
        if (this.label && this.label !== "") {
          return this.label;
        }
        return "";
      }
    }
  }
};
</script>
