<!--
on a plusioeurs mode au depart :
- mode="aggressive" par defaut
- mode="lazy"
- mode="passive"
...
https://logaretm.github.io/vee-validate/guide/interaction-and-ux.html#interaction-modes
-->
<template>
  <div :class="classe">
    <div class="form-group">
      <label :for="id_html" v-html="label" v-if="label"></label>
      <validation-provider
        :name="formated_label"
        :rules="rules"
        :id="id_html"
        v-slot="v"
        class="d-block"
        :mode="mode"
      >
        <input
          :id="id_html"
          class="form-control"
          :class="v.classes"
          :_formated_set_empty="formated_set_empty"
          v-model="input.value"
          :placeholder="placeholder"
          @keyup="event_input"
        />
      </validation-provider>
    </div>
  </div>
</template>
<script>
import { ValidationProvider } from "vee-validate";
/**
 * validations
 */
import "/siteweb/PluginsModules/stephane888/wbu-components/src/components/Fomrs/Elements/valids.js";

export default {
  name: "InputText",
  props: {
    /**
     * Contient l'object de
     */
    f_input: {
      type: [Object]
    },
    label: {
      type: [String, Boolean],
      default: "input type text"
    },
    id_html: {
      type: String,
      default: "edit-inputtypetext"
    },
    classe: {
      type: String,
      default: ""
    },
    required: {
      type: Boolean,
      default: false
    },
    put_empty: {
      type: Boolean,
      default: false
    },
    input: {
      type: Object,
      default: function() {
        return {
          value: ""
        };
      }
    },
    placeholder: {
      type: [String, Number],
      default: ""
    },
    rules: {
      type: [String],
      default: ""
    },
    mode: {
      type: String,
      default: "aggressive"
    }
  },
  data: function() {
    return {
      //valid: true
    };
  },
  mounted() {
    //console.log(this.$parent);
  },
  methods: {
    event_input() {
      this.$emit("ev_input", this.input.value); // by default
    },
    /**
     * cette fonction n'est pas necessaire, car on peut recuperer la valeur du status su champs,  travers le parent '<validation-observer>'
     */
    display_infos(v) {
      this.f_input.valid = v.valid;
      return null;
    },
    set_empty() {
      this.input.value = "";
    }
  },
  components: {
    "validation-provider": ValidationProvider
  },
  computed: {
    formated_label: {
      get() {
        if (this.label && this.label !== "") {
          return this.label;
        }
        return "";
      }
    },
    /**
     * function pas encore ok, pour mettre vide un champs.
     */
    formated_set_empty() {
      //console.log("formated_set_empty");
      if (this.put_empty) {
        this.set_empty();
      }
      return "";
    }
  }
};
</script>
