<template>
  <div :class="classe">
    <div class="form-group">
      <label :for="id_html" v-html="label" v-if="label"></label>
      <validation-provider
        :name="formated_label"
        :rules="rules"
        :id="id_html"
        v-slot="v"
        class="d-block"
        :mode="mode"
      >
        <select
          :id="id_html"
          class="form-control"
          :class="v.classes"
          v-model="input.value"
          :placeholder="placeholder"
          @change="event_input"
        >
          <option v-if="show_empty_option" value="">Choisir...</option>
          <option
            v-for="(option, index) in options"
            v-html="option"
            :value="index"
            :key="index"
          ></option>
        </select>
      </validation-provider>
    </div>
  </div>
</template>
<script>
import { ValidationProvider } from "vee-validate";
/**
 * validations
 */
import "/siteweb/PluginsModules/stephane888/wbu-components/src/components/Fomrs/Elements/valids.js";

export default {
  name: "InputSelect",
  props: {
    label: {
      type: [String, Boolean],
      default: "input type text"
    },
    id_html: {
      type: String,
      default: "edit-inputtypetext"
    },
    classe: {
      type: String,
      default: ""
    },
    required: {
      type: Boolean,
      default: false
    },
    input: {
      type: Object,
      default: function() {
        return {
          value: ""
        };
      }
    },
    placeholder: {
      type: [String, Number],
      default: ""
    },
    rules: {
      type: [String],
      default: ""
    },
    show_empty_option: {
      type: [Boolean],
      default: true
    },
    options: {
      type: [Object, Array],
      default: function() {
        return {
          val1: "option 1",
          val2: "option 2",
          valn: "option n"
        };
      }
    },
    mode: {
      type: String,
      default: "aggressive"
    }
  },
  data: function() {
    return {
      //valid: true
    };
  },
  mounted() {
    //console.log(this.$parent);
  },
  methods: {
    event_input() {
      this.$emit("ev_input", this.input.value); // by default
    }
  },
  components: {
    "validation-provider": ValidationProvider
  },
  computed: {
    formated_label: {
      get() {
        if (this.label && this.label !== "") {
          return this.label;
        }
        return "";
      }
    }
  }
};
</script>
